import React from 'react';
const Book =(props)=> {
    const {img,title,author}=props;
    const clickHandler = () =>{
      alert('Hellow world');
    }
    const complexExample =(author)=>{
      console.log(author);
    }
    return <article>
      <img src={img} alt='' />
      <h2 onClick={()=> console.log(title)}>{title}</h2>
      <h4>{author}</h4>
      <button type="button" onClick={clickHandler}>
        reference example
      </button>
      <button type="button" onClick={()=>complexExample(author)}>More complex ex</button>
    </article>
  }
  
  export default Book;